.navbar{
    background: rgb(30,30,30) ;
    height: 5rem;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top: 0;
    z-index: 3;
}

.Logo{
    position: absolute;
    left: 2rem;
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;
}

.desktopMenuListItem:hover{
    color: #ff6b6b;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #ff6b6b;
}

.desktopMenuBtn{
    background: white;
    color: black;
    margin: 1rem;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding: 0rem 1rem;
    border-radius: 2rem;
}

.desktopMenuImg{    
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem ;
    
}
.desktopMenuList{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.active{
    color: #ff6b6b;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #ff6b6b;
}

.mobMenu{
    display:none;
    object-fit: cover;
    height: 1.8rem;
}

.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40,40,40);
    border-radius: 1rem;
}

.listItem{
    color:white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30,30,30);
}


@media screen and (max-width:728px){
    .desktopMenuList{
        display: none;
    }
    .mobMenu{
        display: flex;
        margin: 0 1.5rem;
    }

    .navbar{
        justify-content: space-between;

    }

    .Logo{
        position: relative;
    }
}

.mobMenu:hover{
    transform: scale(1.15);
    transition: all 0.2s ease-in-out;
}

.Logo:hover{
    transform: scale(1.15);
    transition: all 0.2s ease-in-out;
}